class TextArtDrawer
{
   
    writeMethod:any;
    constructor(writeMethod:any)
    {
      this.writeMethod = writeMethod;
    }

    drawLine(line,resolveFunc)
    {
      
      if(line < drawingOneArray.length)
      {
        this.writeMethod(drawingOneArray[line]);
        setTimeout(()=>{
          this.drawLine(line+1,resolveFunc);
        },150);
      }
      else{
        resolveFunc();
      }
    }

    draw()
    {
      //this.writeMethod(drawingOne);
      return  new  Promise(async (resolve, reject) => {
        this.drawLine(0,resolve);
      
      });
      
    }

}

export default TextArtDrawer;
